
import { defineComponent, reactive, toRefs, watch } from 'vue';

export interface FileUploadItem {
  key: string;
  name: string;
  size: number;
  uploaded: number;
}

export default defineComponent({
  name: 'FileUploadProgress',
  props: {
    keyValue: String,
    name: String,
    size: {
      type: Number,
      default: 0,
    },
    uploaded: {
      type: Number,
      default: 0,
    },
  },
  emits: ['compelete', 'close'],
  setup(props, context) {
    const state = reactive({
      closing: false,
      value: 0,
    });

    watch(
      () => props.uploaded,
      (newValue: number) => {
        state.value = Math.floor((newValue / props.size) * 100);
        if (state.value >= 100 && !state.closing) {
          state.closing = true;
          context.emit('compelete', props.keyValue, props.size);
          setTimeout(() => context.emit('close', props.keyValue), 3000);
        }
      }
    );

    return {
      ...toRefs(state),
    };
  },
});
